:root {
  --ocean-blue: #3568c3;
  --plant-green: #40a32e;
  --neutral-gray: #888888;
  --space-black: #070d20;
  --dark-background: #070d20;
  --standard-transition: all 0.25s linear;
  --med-transition: all 1s ease;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  width: 100vw;
  /* height: 100%; */
}

body {
  width: 100vw;
  /* height: 100%; */
  background: #000000;
  /* min-height: 20000px; */
  overflow-x: hidden;
  overscroll-behavior: none;
}

#root {
  position: relative;
  width: 100vw;
  height: 100%;
  overflow-x: hidden;
  /* min-height: 20000px; */
}

.App {
