}

/* landing page */

canvas {
  position: fixed;
  display: block;
  left: 0;
  height: 100vw;
  width: 100vw;
  z-index: 0;
  /* new */
  overflow: hidden;
  /* touch-action: none; */
  /* pointer-events: none; */
}

.disable-pointer-events {
  pointer-events: none;
}

.nav {
  position: fixed;
  width: 100vw;
  color: white;
  opacity: 0;
  pointer-events: none;
  transition: var(--standard-transition);
  z-index: 3;
}

.nav--show {
  opacity: 1;
  pointer-events: auto;
  /* transform: translateY(200px); */
}

.nav-links {
  width: 95%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  list-style-type: none;
}

@media screen and (max-width: 768px) {
  .nav-links {
    width: 100vw;
    flex-direction: column;
    background-color: var(--space-black);
  }
}

.nav-link {
  padding: 25px;
  font-size: 1.5rem;
  font-family: Raleway, sans-serif;
}

@media screen and (max-width: 768px) {
  .nav-link {
    padding: 10px;
    font-size: 20px;
  }
}

.nav-link:hover {
  cursor: pointer;
}

.nav-icon {
  position: fixed;
  left: 96%;
  top: 24px;
  z-index: 2;
  font-size: 32px;
  color: white;
  z-index: 4;
}

@media screen and (max-width: 768px) {
  .nav-icon {
    left: unset;
    top: 5px;
    right: 5px;
  }
}

.nav-icon:hover {
  cursor: pointer;
}

.rotate-device-sign {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: none;
  z-index: 3;
}

@supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
  .rotate-device-sign {
    background-color: rgba(0, 0, 0, 0.85);
  }
  /* .rotate-device-sign {
    -webkit-backdrop-filter: blur(40px);
    backdrop-filter: blur(40px);
  } */
}

/* slightly transparent fallback for Firefox (not supporting backdrop-filter) */
@supports not ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
  .rotate-device-sign {
    background-color: rgba(0, 0, 0, 0.85);
  }
}

.rotate-sign svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
}

.rotate-device-sign__msg {
  position: absolute;
  left: 50%;
  top: 73%;
  transform: translateX(-50%);
  text-align: center;
  margin: 0 auto;
  font-family: Raleway;
  color: white;
}

