.scroll-down-sign {
  position: fixed;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Raleway, sans-serif;
  /* font-size: 24px; */
  color: white;
  opacity: 0;
  transition: opacity 0.5s linear;
  pointer-events: none;
}

