  color: orange;
  cursor: pointer;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  color: white;
  cursor: pointer;
}

.form-container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 800px;
  height: 600px;
  padding: 25px;
  border: 1px solid var(--neutral-gray);
  background: var(--dark-background);
  font-family: Raleway;
  color: white;
  transform: translate(-50%, -50%);
}

.submission-msg-container {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.submission-msg {
  font-size: 24px;
  margin-bottom: 25px;
}

.form-btn {
  width: 100px;
  padding: 5px;
  border: solid var(--neutral-gray) 1px;
  margin: 0 auto;
  color: white;
  background: var(--dark-background);
  cursor: pointer;
  transition: var(--standard-transition);
}

.form__error {
  min-height: 18px;
  font-size: 16px;
  color: red;
}

/* research section */

.research {
  position: relative;
  width: 100%;
  /* 25px + 25px + 24px */
  padding-top: 75px;
  background: var(--dark-background);
}

