.abstract-container {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 800px;
  height: 600px;
  padding: 25px;
  border: 1px solid var(--neutral-gray);
  background: var(--dark-background);
  transform: translate(-50%, -50%);
}

.close-abstract-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  color: white;
  cursor: pointer;
}

.abstract-container__article-title {
  margin-bottom: 25px;
  color: white;
  font-family: Raleway;
  font-weight: 500;
}

.abstract-container__article-authors {
  margin-bottom: 20px;
  color: white;
  font-family: Raleway;
  font-weight: 500;
}

.abstract-container__abstract {
  color: white;
  font-family: Raleway;
  font-weight: 500;
}

.paper-category {
  position: relative;
  left: 2.5%;
  width: 95%;
}

.paper-category__heading-container {
  margin-bottom: 30px;
  display: flex;
  align-items: center;
}

.paper-category__heading {
  /* position: relative;
  left: 2.5%; */
  /* margin-bottom: 30px; */
  font-size: 32px;
  color: white;
  font-family: Raleway;
  font-weight: 500;
}

.paper-card-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-flow: row;
}

.paper-card {
  margin-bottom: 25px;
  display: flex;
}

.paper-card__img-container {
  width: 100px;
  height: 150px;
}

.paper-card__img {
  height: 100%;
  width: 100%;
}

.paper-card__text-container {
  margin-left: 15px;
  color: white;
  font-family: Raleway;
}

.paper-title {
  margin-bottom: 20px;
  font-size: 20px;
  font-style: italic;
  font-weight: 600;
}

.paper-authors {
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: 600;
}

.paper-journal {
  margin-bottom: 12px;
  font-size: 16px;
  font-style: italic;
  font-weight: 600;
}

.paper-year {
  margin-bottom: 13px;
  font-size: 16px;
  font-weight: 600;
}

.paper-abstract-link {
  display: block;
  margin-bottom: 0;
  margin-top: auto;
  display: flex;
  align-items: center;
  /* font-family: Raleway; */
  font-weight: 600;
  text-decoration: none;
  color: var(--ocean-blue);
}

.paper-category__separator {
  position: relative;
  top: 5%;
  /* left: 2.5%; */
  height: 1px;
  /* width: 95%; */
  width: 100%;
  margin-bottom: 25px;
  background: var(--neutral-gray);
}

.add-paper-form {
  display: flex;
  flex-direction: column;
}

.add-paper-form label {
  margin-bottom: 5px;
}

.add-paper-form input {
  font-family: Raleway;
}

.add-paper-form__form-item-container {
  /* margin-bottom: 15px; */
  margin-bottom: 2px;
  display: flex;
  flex-direction: column;
}

.add-paper-form .year,
.category {
  width: 75%;
}

.add-paper-form__category-year-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.add-paper-form__abstract {
  font-family: Raleway;
  resize: none;
}

.add-paper-form__submit-btn {
  width: 100px;
  padding: 5px;
  border: solid var(--neutral-gray) 1px;
  margin: 0 auto;
  color: white;
  background: var(--dark-background);
  cursor: pointer;
  transition: var(--standard-transition);
}
.add-paper-form__submit-btn:hover {
  color: black;
  background: white;
}

/* footer */

.footer {
  position: relative;
  width: 100vw;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: var(--dark-background);
}

.footer-text {
  font-family: Raleway, sans-serif;
  text-align: center;
  color: white;
}
